import React, { useState, useEffect } from 'react';
import { useSwipeable } from 'react-swipeable'

import EventCard from './EventCard';
import leftArrow from '../../assets/images/arrow-left.svg';
import rightArrow from '../../assets/images/arrow-right.svg';
import './Event.css';

const events = [
    {
        name: 'Открытие',
        day: 'Воскресенье',
        date: '27 Октября 2019',
    },
    {
        name: <span>Первый<br />день</span>,
        day: 'Суббота',
        date: '2 Ноября 2019',

    },
    {
        name: <span>Последний<br />день</span>,
        day: 'Воскресенье',
        date: '3 Ноября 2019',
    },
];

const Event = ({ num, arrowClicked }) => {

    const [windowWidth, setWindowWidth] = useState(window.innerWidth);

    const updateWindowDimensions = () => {
        setWindowWidth(window.innerWidth);
    }

    useEffect(() => {
        window.addEventListener('resize', updateWindowDimensions);
        return () => {
            window.removeEventListener('resize', updateWindowDimensions);
        }
    });

    const handleRightSwipe = () => {
        if (num > 0) {
            arrowClicked(false);
        }
        
    }

    const handleLeftSwipe = () => {
        if (num < 2) {
            arrowClicked(true);
        }
    }

    const handlers = useSwipeable({ onSwipedRight: (eventData) => handleRightSwipe(), onSwipedLeft: (eventData) => handleLeftSwipe() })

    return (
        <div className="events-container margin-from-prev-comp" {...handlers}>
            { num > 0 ?
                <img src={leftArrow} className="arrow arrow-left pointer" alt="Левая стрелка" onClick={() => arrowClicked(false)} /> :
                <div className="hide" /> }
            { num < 2 ? 
                <img src={rightArrow} className="arrow arrow-right pointer" alt="Правая стрелка" onClick={() => arrowClicked(true)} /> :
                <div className="hide" /> }
            <div className="events-header">
                <h2 className="flex-column text-centered white ev-header-style">{events[num].name}</h2>
                <p className="flex-column text-centered white ev-header-style">{events[num].date}<br />{events[num].day}</p>
            </div>
            <div className="flex-column ev-font">
                <div className="ev-cards">
                    <div className="group-label text-centered violet">1 Поток</div>
                    <div className="group-label text-centered blue">2 Поток</div>
                </div>
                <div className="ev-cards">
                    <EventCard 
                        evName='Лекция / интерактивная дискуссия по ведущим святилищам синто в Японии в Средние века'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={1} />
                    {windowWidth > 800 ? <EventCard 
                        evName='Лекция о системе международных отношений на Дальнем Востоке в Средние века'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={2} /> : <div className="hide" />}
                    
                </div>
                <div className="ev-cards">
                    <EventCard 
                        evName='Лекция / интерактивная дискуссия по ведущим святилищам синто в Японии в Средние века'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={1} />
                    {windowWidth > 800 ? <EventCard 
                        evName='Лекция о системе международных отношений на Дальнем Востоке в Средние века'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={2} /> : <div className="hide" />}
                </div>
                <div className="ev-cards">
                    <EventCard 
                        evName='Лекция о Японии'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={1} />
                    {windowWidth > 800 ? <EventCard 
                        evName='Лекция о системе международных отношений на Дальнем Востоке в Средние века'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={2} /> : <div className="hide" />}
                </div>
                <div className="ev-cards">
                    <EventCard 
                        evName='Лекция1'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={1} />
                    {windowWidth > 800 ? <EventCard 
                        evName='Лекция2'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={2} /> : <div className="hide" />}
                </div>
            </div>
        </div>
    );
}

export default Event;